@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  box-sizing: border-box;
}

body {
  background-color: #333;
  color: #ffffff;
}

.g-2048 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 100vh;
  margin: 0 auto;
  flex-wrap: wrap;
  align-content: center;
  position: relative;
}

.top-row {
  display: flex;
  width: 100%;
  justify-content: space-between;

  align-items: center;
}

.score {
  font-size: 30px;
  justify-self: right;
}

.board {
  height: 500px;
  width: 500px;
  border-radius: 6px;
  background-color: #666;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  box-sizing: border-box;
  filter: blur(0px);
  transition: filter 1s;
}

.start-btn {
  padding: 0 20px;
  margin: 10px;
  background-color: #1aa260;
  font-size: 30px;
  text-align: center;
  border-radius: 8px;
  cursor: pointer;
}

.start-btn:active:hover {
  background-color: #ffffff;
  color: black;
}

.square {
  height: 112.25px;
  width: 112.25px;
  background-color: #555;
  font-family: Roboto;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  border-radius: 4px;

  transition: background-color 0.5s;
}

.result {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.status {
  font-size: 40px;
}

.new {
  background-color: #006400;
}

